import $ from "jquery";
$(document).ready(function (){
  $("#demands_btn").click(function (){
      $('html, body').animate({
          scrollTop: $("#demands").offset().top
      }, 1000);
  });
  $("#duties_btn").click(function (){
      $('html, body').animate({
          scrollTop: $("#duties").offset().top
      }, 1000);
  });
  $("#circs_btn").click(function (){
      $('html, body').animate({
          scrollTop: $("#circs").offset().top
      }, 1000);
  });
});